.edifice_main_container .sortlist {
  overflow: auto;
  -webkit-box-shadow: 0 0 15px inset #ccc;
          box-shadow: 0 0 15px inset #ccc;
}
.edifice_main_container .sortlist > li {
    margin: 20px auto;
    width: 98%;
    height: 230px;
}
.edifice_main_container .sortlist > li .floornumber {
      margin: 20px auto;
      text-align: center;
}
.edifice_main_container .sortlist > li .floornumber span {
        display: inline-block;
        width: 40px;
        height: 40px;
        line-height: 40px;
        text-align: center;
        border: 1px solid #ccc;
        font-size: 25px;
        border-radius: 50%;
        vertical-align: middle;
}
.edifice_main_container .sortlist > li .floornumber span p {
          font-size: 15px;
}
.edifice_main_container .sortlist .minus {
    display: block;
    margin: 30px 0 0 0;
}
.edifice_main_container .buildingname {
  margin: 10px 15px;
  min-height: 50px;
  font-size: 15px;
  position: absolute;
  top: 0;
  z-index: 1;
}
.edifice_main_container .buildingname label {
    font-size: 15px;
}
.edifice_main_container .floordesc > li {
  display: inline-block;
}
.edifice_main_container .floordesc .brandmeta {
  display: inline-block;
  width: 240px;
  height: 40px;
}
.edifice_main_container .floordesc .brandmeta label {
    display: inline-block;
    width: 120px;
    text-align: right;
    vertical-align: middle;
}
.edifice_main_container .advertisedesc {
  display: block;
  width: 100%;
}
.edifice_main_container .advertisedesc .brandicon {
    display: inline-block;
    width: 300px;
    vertical-align: top;
}
.edifice_main_container .advertisedesc .advertiselist {
    display: inline-block;
    width: 1150px;
    vertical-align: top;
}
.edifice_main_container .advertisedesc .advertiselist > label {
      display: inline-block;
      vertical-align: top;
}
.edifice_main_container .advertisedesc .advertiselist .content {
      display: inline-block;
      vertical-align: top;
}
.edifice_main_container .advertisedesc .advertiselist .content > ul .brandmeta {
        display: inline-block;
        margin: 0 10px 0 0;
        width: 360px;
}
.edifice_main_container .advertisedesc .advertiselist .content > ul .brandmeta img {
          display: inline-block;
          width: 120px;
}
.edifice_main_container .advertisedesc .advertiselist .content > ul .brandmeta .advertisemeta {
          display: inline-block;
          vertical-align: top;
}
.edifice_main_container .advertisedesc .advertiselist .content > ul .brandmeta .advertisemeta .location {
            width: 100px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
}
.edifice_main_container .operation {
  float: right;
}
.edifice_sortadvertise_wrapper li {
  display: inline-block;
  margin: 10px;
  width: 135px;
  height: 135px;
  text-align: center;
  border: 1px solid #ccc;
  border-radius: 5px;
  vertical-align: middle;
}
.edifice_sortadvertise_wrapper li img {
    display: inline-block;
    width: 100%;
    vertical-align: middle;
}
.edifice_sortadvertise_wrapper li:before {
    content: '';
    display: inline-block;
    vertical-align: middle;
}
.edifice_sortadvertise_wrapper li:hover {
    cursor: move;
}
.edifice_addminusadvertise_wrapper {
  margin: 10px 0;
  position: relative;
  text-align: right;
}
.edifice_addminusadvertise_wrapper .minus {
    display: inline-block;
    width: 30px;
    text-align: center;
    position: absolute;
    right: -40px;
    top: 0;
}
.edifice_addminusadvertise_wrapper .minus a {
      display: inline-block;
      width: 20px;
      height: 20px;
      font-size: 20px;
      text-align: center;
      color: orangered;
      vertical-align: middle;
}

